body {
  .ant-tooltip.tool-top-right {
    padding-bottom: 0;

    .ant-tooltip-arrow {
      transform: rotateX(180deg);
      right: 2px;
    }
  }

  .ant-table-tbody {
    > tr:hover:not(.ant-table-expanded-row) > td,
    .ant-table-row-hover,
    .ant-table-row-hover > td {
      background: #e0e8ff;
    }
  }

  // for no-data
  .no-data {
    .ant-table-tbody {
      > tr:hover:not(.ant-table-expanded-row) > td,
      .ant-table-row-hover,
      .ant-table-row-hover > td {
        background: transparent;
      }
    }
  }

  .nav-container .userList-container .no-data tbody tr td:first-child {
    width: 100%;
  }
}
.countDatas {
  margin-top: 30px;
}
.footer-container {
  margin: 40px 0 20px;
  text-align: center;
  color: #5a5e89;
  opacity: 0.8;
  a,
  span {
    white-space: nowrap;
  }
  a {
    color: #5a5e89;

    &:hover {
      color: #3d56d6;
      opacity: 1;
    }
  }

  .line {
    margin: 0 10px;
  }
}

.nav-container {
  display: flex;
  justify-content: space-between;
  height: 500px;

  .account-container {
    width: 320px;
    background: #fff;
    padding: 20px;
    position: relative;
    border-radius: 8px;

    .title {
      .content {
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
        color: #0f134f;
      }

      .tip {
        display: inline-block;
        width: 20px;
        height: 20px;
        line-height: 20px;
        text-align: center;
        border-radius: 20px;
        border: 1px solid #555;
        cursor: pointer;
      }
    }

    .key {
      color: #999;
      font-size: 13px;
    }

    .value {
      color: #111;
      font-size: 16px;

      .fw500 {
        white-space: pre-wrap;
        line-height: 30px;
      }
    }

    .more {
      margin-top: 20px;
      padding-top: 16px;
      border-top: 1px solid #f2f2f2;

      p {
        display: flex;
        justify-content: space-between;
      }

      .with-icon {
        background: url(../images/tooltipIcon.svg) right center no-repeat;
        width: auto;
        display: inline-block;
        padding-right: 20px;
        cursor: pointer;
      }
    }
  }

  .intro-container {
    height: calc(100% - 92px);
    display: flex;
    flex-direction: column;
    .intro-bg {
      width: 100%;
      flex: 1;
      background: linear-gradient(to bottom right, #4a59df, #6f66f5);
      background-size: 100% 100%;
      border-radius: 8px;
      position: relative;

      &.intro-bg-mobile {
        height: 130px;
      }

      .intro-image {
        width: 100%;
        height: 100%;
        background: url(../images/intro.png) right center no-repeat;
        background-size: auto 400px;
        margin-bottom: 20px;
      }

      .intro-text {
        width: 320px;
        position: absolute;
        left: 12%;
        top: 50%;
        transform: translateY(-50%);
        color: #fff;
        font-size: 30px;

        &.english-text {
          width: 393px;
        }

        .intro-title {
          display: flex;
          align-items: center;
          justify-content: left;

          > span {
            display: inline-block;
            font-size: 24px;

            &:first-child {
              width: 126px;
              height: 28px;
              background: url(../images/mobileMainLogo.svg) 0 0 no-repeat;
              margin-right: 10px;
              background-size: 100%;
            }
          }
        }

        .intro-subtitle {
          width: 100%;
          text-align: justify;
          text-align-last: justify;
          font-size: 24px;

          > span {
            color: #fff;
            font-size: 24px;
            font-weight: 600;

            > span {
              color: #ffe500;
              font-size: 24px;
              font-weight: 600;
            }
          }
        }
      }
    }
  }

  .nav-right-container {
    width: calc(100% - 340px);
    height: auto;
  }
  // valueIntro
  .value-intro {
    display: flex;
    flex-direction: row;
    background: #ffffff;
    box-shadow: 0px 10px 40px rgba(205, 205, 205, 0.1);
    border-radius: 8px;
    margin-bottom: 20px;
    padding: 16px 35px;
    color: #0f134f;
    text-align: right;
    .value-name {
      font-family: 'PingFang SC';
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      color: #545669;
      line-height: 19px;
      height: 19px;
      display: inline-block;
      margin: 0 20px 0 0;
      vertical-align: top;
      margin-bottom: 4px;
    }
    .tooltip-icon {
      width: 12px;
      height: 12px;
      background-size: contain;
      position: relative;
      top: 2px;
      left: 2px;
    }
    .value-number {
      font-family: 'Rubik';
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #0f134f;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      display: inline-block;
      vertical-align: top;
    }

    .value-item:first-child {
      text-align: left;
      border-right: 1px solid rgba(132, 134, 158, 0.4);
      padding-left: 0;
      .value-number {
        font-family: 'Rubik-Medium';
        font-size: 20px;
        color: #3d56d6;
      }
      .value-name {
        color: #3d56d6;
        font-size: 20px;
      }
    }
    .value-item {
      flex: 1;
      display: flex;
      flex-direction: column;
      text-align: left;
      padding-left: 4%;
    }
  }

  .userList-container {
    width: 100%;
    height: calc(100% - 92px);
    display: flex;
    flex-direction: column;

    .userList-container-box {
      position: relative;
      padding: 0 20px;
      background: #fff;
      border-radius: 8px;
      flex: 1;
      max-height: 406px;

      .mint-tip {
        background: #f5f6f9;
        border-radius: 2px;
        height: 50px;
        line-height: 50px;
        font-family: PingFang SC;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        color: #84869e;
        padding: 0 12px;
        display: flex;
        align-items: center;

        .ml-80 {
          margin-left: 80px;
        }

        .with-logo {
          background: url(../images/tooltipIcon.svg) right no-repeat;
          background-size: 16px;
          padding-right: 24px;
        }

        .black {
          font-family: Roboto;
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 18px;
          color: #0f134f;
          margin-left: 8px;
        }
      }

      .mint-detail-title {
        font-family: PingFang SC;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 16px;
        color: #0f134f;
        margin: 12px 0;
      }
    }

    table,
    tbody,
    thead {
      display: block;
      width: 100%;
      height: auto;
      cursor: pointer;

      tr {
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: auto;
        cursor: pointer;
        align-items: center;

        th,
        td {
          display: inline-block;
          padding: 0 10px;
          border: none;

          &:first-child {
            width: 34%;
          }

          &:nth-child(2) {
            width: 33%;
            text-align: right;
          }

          &:nth-child(3) {
            width: 33%;
            text-align: right;
          }

          &:nth-child(4) {
            width: 33%;
            text-align: right;
          }

          &:nth-child(5) {
            width: 25%;
          }
        }

        td {
          padding: 12px 10px;

          .my-switch {
            margin-top: 14px;
          }
        }
      }

      thead {
        tr {
          background: #f7f8fb;
        }
      }

      tbody {
        tr {
          border-bottom: 1px solid #f0f0f0;

          &:last-child {
            border: none;
          }
        }
      }
    }

    .mint-table {
      table,
      tbody,
      thead {
        tr {
          cursor: default !important;

          th,
          td {
            &:nth-child(1) {
              width: 40%;
            }

            &:nth-child(2) {
              width: 25%;
              text-align: right;
            }

            &:nth-child(3) {
              width: 35%;
              text-align: right;
            }
          }
        }
      }
    }

    .collateralSymbol {
      display: flex;
      justify-content: flex-start;

      .tokenDetail {
        margin-left: 15px;

        .token-names {
          color: #0f134f;
          font-size: 14px;
        }

        .description {
          color: #84869e;
          font-size: 12px;
        }
      }
    }

    button {
      &:disabled {
        padding: 4px 12px;
        margin: 0 5px;
        border: none;
        border-radius: 5px;
        color: #111;
        background: #dfdfdf;
        cursor: unset;

        &.table-btn {
          padding: 0 6px;
          cursor: not-allowed;
        }
      }
    }
  }
}

td.ant-table-column-sort {
  background: transparent;
}

.mr-2 {
  margin-right: 2px;
}
.mr-4 {
  margin-right: 4px;
}
.mr-6 {
  margin-right: 6px;
}

.more-link {
  float: right;
  color: #0f134f;
  cursor: pointer;
  > span {
    font-weight: 400;
    font-size: 13px;
  }
  &:hover {
    text-decoration: underline;
    color: #3d56d6;
  }
  .right-arrow {
    position: relative;
    display: inline-block;
    width: 10px;
    height: 10px;
    background: url(../images/arrow-right-black-new.svg) right center no-repeat;
    background-size: 6px 10px;
    &:hover {
      background-image: url(../images/arrow-right-blue-new.svg);
    }
  }
}
.collateral-new {
  height: 66px;
  line-height: 66px;
  display: flex;
  justify-content: flex-start;
  > img {
    width: 24px;
    height: 24px;
    position: absolute;
    top: 19px;
    left: 5px;
  }

  .tokenDetail {
    height: 66px;
    line-height: 66px;
    > div {
      text-indent: 30px;
      color: #0f134f;
      font-size: 14px;
      line-height: 16px;
      margin-top: 14px;
      margin-bottom: 4px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      &:last-child {
        color: #84869e;
        font-size: 12px;
        line-height: 14px;
        margin: 0;
      }
    }

    .token-names {
      text-indent: 30px;
      color: #0f134f;
      font-size: 14px;
    }

    .description {
      text-indent: 30px;
      color: #84869e;
      font-size: 12px;
    }
  }
}
.header-warning {
  display: flex;
  align-items: center;
  min-height: 44px;
  background: rgba(239, 86, 86, 0.06);
  border: 1px solid rgba(239, 86, 86, 0.4);
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 0 16px;
  .icon {
    background: #ef5656;
    color: #fff;
    display: inline-block;
    width: 14px;
    height: 14px;
    text-align: center;
    line-height: 14px;
    font-size: 12px;
    margin-right: 8px;
    border-radius: 14px;
    flex-shrink: 0;
  }
}
.show-all-markets {
  position: absolute;
  right: 12px;
  top: 12px;
  font-family: 'PingFang SC';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  color: #0f134f;
  > span {
    font-size: 12px;
  }
  &.show-all-markets-m {
    // position: static;
    display: inline-flex;
    padding-right: 5px;
    > span {
      // border-bottom: 1px dashed #545669;
      padding: 0;
      margin-left: 10px;
      > span {
        font-family: 'PingFang SC';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #545669;
      }
    }
    &:hover {
      > span > span {
        color: #3d56d6;
      }
    }
    .ant-checkbox {
      position: relative;
      top: 0px;
      border: none;
    }
    .ant-checkbox-inner {
      width: 12px;
      height: 12px;
      background: #d1d7e9;
      border-radius: 2px;
      border-bottom: none;
    }
    .ant-checkbox-checked .ant-checkbox-inner {
      background: #3d56d6;
    }
    &.ant-checkbox-wrapper-checked {
      > span {
        // border-bottom: 1px dashed #3d56d6;
        > span {
          color: #3d56d6;
        }
      }
    }
    .ant-checkbox-inner::after {
      border-width: 1px;
    }
  }
}
.infoList-container {
