.table-btn {
  &.deposit-btn,
  &.lend-btn,
  &.withdraw-btn,
  &.giveback-btn {
    min-width: 60px;
    height: 30px;
    line-height: 30px;
    padding: 0 6px;
    text-align: center;
    font-size: 12px;
    color: #3d56d6;
    background: rgba(61, 86, 214, 0.06);
    border: 1px solid #3d56d6;
    border-radius: 4px;
    cursor: pointer;
  }

  &.lend-btn {
    background: rgba(24, 193, 159, 0.05);
    border: 1px solid #18c19f;
    color: #18c19f;
  }

  &.withdraw-btn {
    background: #773dd6;
  }

  &.giveback-btn {
    background: #ff8718;
  }

  &:disabled {
    cursor: not-allowed;
    background: #dfdfdf;
    color: #111;
  }
}

.ant-table-tbody tr:hover {
  .table-btn {
    &.deposit-btn,
    &.lend-btn {
      background: #3d56d6;
      border-radius: 4px;
      color: #ffffff;
    }

    &.lend-btn {
      background: #18c19f;
      border: 1px solid #18c19f;
      border-radius: 4px;
      color: #ffffff;
    }
  }
}

