.home-notice {
  display: flex;
  align-items: center;
  min-height: 44px;
  line-height: 24px;
  background: rgba(61, 86, 214, 0.06);
  border: 1px solid rgba(61, 86, 214, 0.4);
  border-radius: 8px;
  padding: 12px 20px;
  margin-bottom: 20px;

  .loudspeaker {
    display: inline-block;
    width: 18px;
    height: 18px;
    background: url(../images/loudspeaker.svg) 0 0 no-repeat;
    background-size: 100%;
    margin-right: 4px;
  }

  .notice-content {
    width: calc(100% - 20px);

    > a {
      font-family: PingFang SC;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      color: #0f134f;
    }
  }
}

.mortgage-modal {
  .ant-modal-header {
    padding: 20px 24px;
  }
  .mortgage-modal-body {
    line-height: 24px;
    font-size: 14px;
    font-family: PingFang SC;
    color: #0f134f;
    text-align: left;
  }
  .collateral_btn {
    width: 90px;
    height: 34px;
    line-height: 34px;
    margin-top: 20px;
    font-size: 14px;
    color: #fff;
    text-align: center;
    border: none;
    border-radius: 8px;
    background: #3d56d6;
  }
}

.tac {
  text-align: center !important;
}

.header-toast {
  position: relative;
  min-height: 44px;
  line-height: 24px;
  background: rgba(61, 86, 214, 0.06);
  border: 1px solid rgba(61, 86, 214, 0.4);
  border-radius: 8px;
  padding: 6px 20px;
  margin-bottom: 20px;

  .notice-more {
    position: absolute;
    top: 16px;
    right: 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
    > span {
      display: inline-block;
      font-family: 'PingFang SC';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #0f134f;
    }
    > img {
      margin-left: 7.5px;
    }
  }

  .white-close {
    right: 10px;
  }

  .toast-text {
    display: inline-block;
    // padding-left: 33px;
    // background: url(../images/laba.svg) left no-repeat;
    height: 32px;
    overflow: hidden;
    // margin-left: -25px;

    ul {
      display: block;
      height: 40px;
      line-height: 20px;
      margin: 0;
      position: relative;
      padding: 0;

      &.animate {
        -webkit-animation: 7.5s rowup linear infinite normal;
        animation: 7.5s rowup linear infinite normal;
      }
    }

    li {
      // line-height: 22px;
      // padding: 9px 0;
      // height: 40px;
      // display: block;
      // text-align: left;
    }

    @keyframes rowup {
      0% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
      }

      5% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      33% {
        -webkit-transform: translate3d(0, -40px, 0);
        transform: translate3d(0, -40px, 0);
      }

      38% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      66% {
        -webkit-transform: translate3d(0, -80px, 0);
        transform: translate3d(0, -80px, 0);
      }

      71% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }

      100% {
        -webkit-transform: translate3d(0, -120px, 0);
        transform: translate3d(0, -120px, 0);
      }
    }

    a {
      font-family: PingFang SC;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      color: #0f134f;
      line-height: 14px;
      padding: 0;
      height: 40px;
      display: flex;
      // justify-content: center;
      align-items: center;

      span {
        display: block;
        font-size: 14px;
        // max-width: 290px;
      }
    }

    a .link {
      font-size: 12px;
      line-height: 15px;
      color: #30084c;
      background: #ffffff;
      border-radius: 23px;
      padding: 2px 10px;
      margin-left: 0;
      display: inline-block;
      height: 17px;
      vertical-align: top;
      margin-top: 2px;
      display: none;
    }
  }
}

